/* LoadingAnimation.css - Professional Deepavali Design */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #0a0a0a;
    z-index: 9999;
    overflow: hidden;
}

/* Elegant gradient background */
.gradient-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at 30% 20%, rgba(255, 107, 0, 0.15) 0%, transparent 50%),
                radial-gradient(ellipse at 70% 80%, rgba(255, 179, 0, 0.12) 0%, transparent 50%),
                radial-gradient(ellipse at 50% 50%, rgba(216, 27, 96, 0.08) 0%, transparent 70%);
}

/* Premium particle system */
.particle {
    position: absolute;
    width: 2px;
    height: 2px;
    background: rgba(255, 215, 0, 0.6);
    border-radius: 50%;
    pointer-events: none;
    box-shadow: 0 0 10px rgba(255, 215, 0, 0.4);
}

/* Light rays from center */
.light-rays {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.light-ray {
    position: absolute;
    width: 2px;
    height: 50%;
    background: linear-gradient(to bottom, transparent, rgba(255, 179, 0, 0.1), transparent);
    top: 50%;
    left: 50%;
    transform-origin: top center;
    transform: translateX(-50%) rotate(calc(var(--ray-index) * 30deg));
}

/* Main content container */
.content-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 10;
    padding: 2rem;
}

/* Geometric mandala */
.mandala-container {
    position: absolute;
    width: 600px;
    height: 600px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    z-index: 1;
}

.mandala-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid rgba(255, 179, 0, 0.15);
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.mandala-layer::before,
.mandala-layer::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background: rgba(255, 179, 0, 0.1);
    top: 50%;
    left: 0;
}

.mandala-layer::after {
    transform: rotate(60deg);
}

.mandala-layer:nth-child(1) { width: 100%; height: 100%; }
.mandala-layer:nth-child(2) { width: 80%; height: 80%; border-color: rgba(255, 179, 0, 0.2); }
.mandala-layer:nth-child(3) { width: 60%; height: 60%; border-color: rgba(255, 179, 0, 0.25); }
.mandala-layer:nth-child(4) { width: 40%; height: 40%; border-color: rgba(255, 179, 0, 0.3); }
.mandala-layer:nth-child(5) { width: 20%; height: 20%; border-color: rgba(255, 179, 0, 0.4); }

.mandala-center {
    position: absolute;
    width: 60px;
    height: 60px;
    background: radial-gradient(circle, rgba(255, 179, 0, 0.3), transparent);
    border: 2px solid rgba(255, 179, 0, 0.5);
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 30px rgba(255, 179, 0, 0.3),
                inset 0 0 20px rgba(255, 179, 0, 0.2);
}

/* Premium diya design */
.premium-diya {
    position: absolute;
    bottom: 15%;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    z-index: 5;
}

.diya-glow {
    position: absolute;
    width: 200px;
    height: 200px;
    background: radial-gradient(circle, rgba(255, 179, 0, 0.3) 0%, transparent 70%);
    border-radius: 50%;
    top: -80px;
    left: 50%;
    transform: translateX(-50%);
    filter: blur(40px);
}

.diya-base {
    position: relative;
    width: 80px;
    height: 40px;
}

.diya-body {
    width: 80px;
    height: 30px;
    background: linear-gradient(180deg, #c9a566 0%, #8b6f47 100%);
    border-radius: 0 0 40px 40px;
    position: relative;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4),
                inset 0 -2px 5px rgba(0, 0, 0, 0.3);
}

.diya-body::before {
    content: '';
    position: absolute;
    top: 0;
    left: 10%;
    width: 80%;
    height: 3px;
    background: linear-gradient(90deg, transparent, rgba(255, 215, 0, 0.3), transparent);
}

.diya-wick {
    position: absolute;
    width: 2px;
    height: 8px;
    background: #3a2d1f;
    top: -8px;
    left: 50%;
    transform: translateX(-50%);
}

.flame {
    position: absolute;
    width: 30px;
    height: 45px;
    background: linear-gradient(to top, 
        rgba(255, 107, 0, 1) 0%, 
        rgba(255, 179, 0, 0.9) 40%, 
        rgba(255, 213, 79, 0.7) 70%,
        rgba(255, 255, 200, 0.3) 100%);
    border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    filter: blur(1px);
    box-shadow: 0 0 20px rgba(255, 179, 0, 0.6),
                0 0 40px rgba(255, 107, 0, 0.4),
                0 0 60px rgba(255, 179, 0, 0.2);
}

.flame::before {
    content: '';
    position: absolute;
    width: 15px;
    height: 25px;
    background: linear-gradient(to top, 
        rgba(255, 179, 0, 1) 0%, 
        rgba(255, 213, 79, 0.8) 50%,
        rgba(255, 255, 255, 0.6) 100%);
    border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
    top: 5px;
    left: 50%;
    transform: translateX(-50%);
    filter: blur(0.5px);
}

.flame::after {
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    top: -3px;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.8);
}

/* Name styling */
.name-container {
    font-family: 'Georgia', serif;
    font-size: 6rem;
    font-weight: 300;
    letter-spacing: 0.1em;
    color: #fff;
    margin-bottom: 1.5rem;
    text-shadow: 0 2px 30px rgba(255, 179, 0, 0.3);
}

.name-letter {
    display: inline-block;
    opacity: 0;
}

/* Slogan */
.slogan {
    font-family: 'Georgia', serif;
    font-size: 1.4rem;
    color: rgba(255, 255, 255, 0.7);
    font-style: italic;
    text-align: center;
    max-width: 800px;
    line-height: 1.8;
    margin-bottom: 2rem;
    opacity: 0;
    letter-spacing: 0.02em;
}

/* Blessings */
.blessing-container {
    text-align: center;
    margin-top: 2rem;
}

.hindi-text {
    font-size: 2.5rem;
    font-weight: 600;
    color: #ffb300;
    margin-bottom: 0.5rem;
    opacity: 0;
    text-shadow: 0 0 20px rgba(255, 179, 0, 0.4);
    letter-spacing: 0.05em;
}

.english-text {
    font-size: 1.6rem;
    font-weight: 300;
    color: rgba(255, 213, 79, 0.9);
    opacity: 0;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    text-shadow: 0 0 15px rgba(255, 213, 79, 0.3);
}

/* Corner ornaments */
.corner-ornament {
    position: absolute;
    width: 100px;
    height: 100px;
    border: 1px solid rgba(255, 179, 0, 0.2);
    z-index: 2;
}

.corner-ornament.top-left {
    top: 2rem;
    left: 2rem;
    border-width: 1px 0 0 1px;
}

.corner-ornament.top-right {
    top: 2rem;
    right: 2rem;
    border-width: 1px 1px 0 0;
}

.corner-ornament.bottom-left {
    bottom: 2rem;
    left: 2rem;
    border-width: 0 0 1px 1px;
}

.corner-ornament.bottom-right {
    bottom: 2rem;
    right: 2rem;
    border-width: 0 1px 1px 0;
}

.corner-ornament::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border: 1px solid rgba(255, 179, 0, 0.3);
    border-radius: 50%;
}

.corner-ornament.top-left::before {
    top: -10px;
    left: -10px;
}

.corner-ornament.top-right::before {
    top: -10px;
    right: -10px;
}

.corner-ornament.bottom-left::before {
    bottom: -10px;
    left: -10px;
}

.corner-ornament.bottom-right::before {
    bottom: -10px;
    right: -10px;
}

/* RESPONSIVE DESIGN */
@media (max-width: 1024px) {
    .mandala-container {
        width: 500px;
        height: 500px;
    }
    
    .name-container {
        font-size: 4.5rem;
    }
    
    .slogan {
        font-size: 1.2rem;
    }
}

@media (max-width: 768px) {
    .mandala-container {
        width: 400px;
        height: 400px;
    }
    
    .name-container {
        font-size: 3.5rem;
    }
    
    .slogan {
        font-size: 1.1rem;
        padding: 0 2rem;
    }
    
    .hindi-text {
        font-size: 2rem;
    }
    
    .english-text {
        font-size: 1.3rem;
    }
    
    .premium-diya {
        bottom: 10%;
        transform: translateX(-50%) scale(0.8);
    }
    
    .corner-ornament {
        width: 60px;
        height: 60px;
    }
}

@media (max-width: 480px) {
    .mandala-container {
        width: 300px;
        height: 300px;
    }
    
    .name-container {
        font-size: 2.5rem;
    }
    
    .slogan {
        font-size: 1rem;
        padding: 0 1.5rem;
    }
    
    .hindi-text {
        font-size: 1.7rem;
    }
    
    .english-text {
        font-size: 1.1rem;
    }
    
    .premium-diya {
        bottom: 8%;
        transform: translateX(-50%) scale(0.6);
    }
    
    .corner-ornament {
        width: 40px;
        height: 40px;
    }
    
    .corner-ornament.top-left,
    .corner-ornament.top-right {
        top: 1rem;
    }
    
    .corner-ornament.bottom-left,
    .corner-ornament.bottom-right {
        bottom: 1rem;
    }
    
    .corner-ornament.top-left,
    .corner-ornament.bottom-left {
        left: 1rem;
    }
    
    .corner-ornament.top-right,
    .corner-ornament.bottom-right {
        right: 1rem;
    }
}